<template>
  <component v-bind:is="show" @changeShow="changeShow"/>
</template>

<script>
import List from '@/components/board/Board-list';
import Write from '@/components/board/Board-write';

export default {
  name: 'Board',
  components: { List, Write },
  data() {
    return {
      show: 'List',
    };
  },
  methods: {
    changeShow(show) {
      this.show = show;
    },
  },
};
</script>



